import React, { Fragment } from "react";
import { Modal } from "semantic-ui-react";
import {
  Avatar,
  IconButton,
  Button,
  CircularProgress,
} from "@material-ui/core";
import Search from "../Search/Search";
import url from "../../strings/url";
import { CloseOutlined } from "@material-ui/icons";
import "./chat.css";
import { connect } from "react-redux";
import { Result } from "antd";
class AddConversation extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedPerson: null,
      message: "",
      error: false,
      loading: false,
    };
    this.handleSendMessage = this.handleSendMessage.bind(this);
  }

  handleSendMessage() {
    const headers = new Headers();
    headers.append("Authorization", this.props.token);
    this.setState({ loading: true });
    headers.append("content-type", "application/json");
    const body = JSON.stringify({
      message: this.state.message,
      messageTo: this.state.selectedPerson._id,
    });
    fetch(`${url}/startConversation`, { method: "POST", headers, body })
      .then((res) => {
        if (res.ok) {
          this.props.onSessionCreated();
          this.props.onClose();
          return;
        }
        this.setState({ loading: false, error: true });
      })
      .catch((e) => {
        this.setState({ loading: false, error: true });
      });
  }

  render() {
    return (
      <Modal
        className="comingFromLeftP5"
        open={true}
        onClose={this.props.onClose}
        dimmer="inverted"
      >
        <Modal.Header style={{ fontFamily: "poppins" }}>
          Start New Conversations
        </Modal.Header>
        <Modal.Content>
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            {this.state.selectedPerson ? (
              <SelectedPerson
                firstName={this.state.selectedPerson.firstName}
                LastName={this.state.selectedPerson.LastName}
                profilephoto={this.state.selectedPerson.profilephoto}
                cancelSelected={() => {
                  this.setState({ selectedPerson: null, error: false });
                }}
              ></SelectedPerson>
            ) : (
              <React.Fragment>
                <Search
                  forConversation={true}
                  alreadyConnectedUsers={this.props.alreadyConnectedUsers}
                  personSelected={(person) => {
                    this.setState({ selectedPerson: person });
                  }}
                ></Search>
                <div style={{ fontWeight: "bold", fontFamily: "poppins" }}>
                  Search For People....
                </div>
              </React.Fragment>
            )}
          </div>
          {this.state.selectedPerson ? (
            <div
              className="comingFromLeftP5"
              style={{
                display: "flex",
                justifyContent: "center",
                width: "100%",
                margin: 20,
              }}
            >
              <input
                value={this.state.message}
                style={{
                  boxShadow: "none",
                  border: "1px solid black",
                  borderRadius: 3,
                }}
                onChange={(e) => {
                  this.setState({ message: e.target.value, error: false });
                }}
                className="chatInputField"
                placeholder={`Say hi To ${this.state.selectedPerson.firstName} `}
              ></input>
            </div>
          ) : (
            ""
          )}
          {this.state.loading ? (
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                height: "10vh",
              }}
            >
              <CircularProgress style={{ color: "black" }}></CircularProgress>
            </div>
          ) : (
            ""
          )}
          {this.state.error ? (
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                height: "20vh",
                fontWeight: "bold",
                fontFamily: "poppins",
                fontSize: 20,
              }}
            >
              Error Sending Message
            </div>
          ) : (
            ""
          )}
        </Modal.Content>
        {this.state.selectedPerson ? (
          <Modal.Actions>
            <Button
              style={{ marginBottom: 10 }}
              variant="outlined"
              color="primary"
              disabled={this.state.message.length === 0}
              onClick={this.handleSendMessage}
            >
              Send
            </Button>
            <Button
              style={{ marginBottom: 10 }}
              onClick={() => this.props.onClose()}
            >
              close
            </Button>
          </Modal.Actions>
        ) : (
          ""
        )}
      </Modal>
    );
  }
}

function SelectedPerson({ profilephoto, firstName, LastName, cancelSelected }) {
  return (
    <Fragment>
      <div
        className="comingFromRightP5"
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <Avatar src={profilephoto} style={{ marginRight: 10 }}></Avatar>
        <div style={{ fontFamily: "poppins", fontWeight: "bold" }}>
          {firstName + " " + LastName}
        </div>
        <IconButton
          onClick={() => {
            cancelSelected();
          }}
        >
          <CloseOutlined></CloseOutlined>
        </IconButton>
      </div>
    </Fragment>
  );
}

export default connect((state) => ({ token: state.userReducer.token }))(
  AddConversation
);
