import url from "../../strings/url";
import React from "react";
import ChatWindow from "./ChatWindow";
import {
  Grid,
  CircularProgress,
  IconButton,
  Snackbar,
} from "@material-ui/core";
import ChatSessions from "./ChatSessions";
import { Responsive } from "semantic-ui-react";
import { Backspace, AddOutlined, ReplayOutlined } from "@material-ui/icons";
import Socket from "socket.io-client";
import AddConversation from "./AddCoversation";
import { connect } from "react-redux";
import moment from "moment";
const Chat = (props) => {
  const [loading, setLoading] = React.useState(true);
  const [error, setError] = React.useState(false);
  const [sessions, setSessions] = React.useState([]);
  const [session, setSession] = React.useState();
  const [socket, setSocket] = React.useState(Socket.connect(`${url}/chat`));
  const [token, setToken] = React.useState(props.token);
  const [alreadyConnectedUsers, setACU] = React.useState({});
  const [OpenAddConversationModal, setOACM] = React.useState(false);
  const [newConversation, setNewConversation] = React.useState(false);
  const handleOnSomeoneOnline = (id) => {
    setSessions((prev) => {
      return prev.map((session) => {
        if (session.personNextSide.id === id) {
          return {
            ...session,
            personNextSide: {
              ...session.personNextSide,
              Online: { status: "Online", lastSeen: null },
            },
          };
        }
        return session;
      });
    });
  };
  const handleOnSomeoneOffline = (id) => {
    setSessions((prev) => {
      return prev.map((session) => {
        if (session.personNextSide.id === id) {
          return {
            ...session,
            personNextSide: {
              ...session.personNextSide,
              Online: { status: "Offline", lastSeen: new Date() },
            },
          };
        }
        return session;
      });
    });
  };

  const handleOnUnReadMessage = (sessionId) => {
    setSessions((prev) => {
      try {
        const index = prev.findIndex(({ session }) => {
          return session === sessionId;
        });

        if (index === -1) {
          setNewConversation(true);
          return prev;
        }

        const ans = [...prev];
        ans[index] = {
          ...prev[index],
          UnReadMessageCount: prev[index].UnReadMessageCount.map((object) => {
            if (object.forParticipant === props.MyId) {
              return {
                count: object.count + 1,
                forParticipant: object.forParticipant,
              };
            }
            return object;
          }),
        };
        return ans;
      } catch (e) {
        console.log(e.message);
      }
    });
  };

  const handleMakeZero = (sessionId) => {
    setSessions((prev) => {
      const index = prev.findIndex(({ session }) => {
        return session === sessionId;
      });
      const ans = [...prev];
      ans[index] = {
        ...prev[index],
        UnReadMessageCount: prev[index].UnReadMessageCount.map((object) => {
          if (object.forParticipant === props.MyId) {
            return {
              count: 0,
              forParticipant: object.forParticipant,
            };
          }
          return object;
        }),
      };
      return ans;
    });
  };

  const handleFetchSessions = () => {
    const headers = new Headers();
    setLoading(true);
    setSessions([]);
    headers.append("Authorization", token);
    headers.append("content-type", "application/json");
    fetch(`${url}/getMySessions`, { method: "GET", headers })
      .then(async (res) => {
        try {
          if (res.ok) {
            const sessions = await res.json();

            sessions.sort((a, b) => {
              return -1 * moment(a.lastMessage).diff(b.lastMessage, "seconds");
            });

            setLoading(false);
            setSessions(sessions);

            for (let i = 0; i < sessions.length; i++) {
              const { id } = sessions[i].personNextSide;
              setACU((prev) => ({ ...prev, [id]: true }));
            }
            return;
          }
          setLoading(false);
          setError(true);
        } catch (e) {
          console.log(e.message);
        }
      })
      .catch((e) => {
        setLoading(false);
        setError(true);
      });
  };

  React.useEffect(() => {
    handleFetchSessions();
    socket.emit("Save_Id", {
      token: token,
      session: null,
    });
    socket.on("unreadMessage", (data) => {
      handleOnUnReadMessage(data.session);
    });

    socket.on("someOneGetsOnline", (data) => {
      handleOnSomeoneOnline(data.id);
    });
    socket.on("someOneGetsOffline", (data) => {
      handleOnSomeoneOffline(data.id);
    });

    return () => {
      socket.disconnect();
    };
  }, []);

  return (
    <React.Fragment>
      <Snackbar
        open={newConversation}
        autoHideDuration={6000}
        onClose={() => {
          setNewConversation(false);
        }}
        ContentProps={{ style: { fontFamily: "poppins" } }}
        anchorOrigin={{ horizontal: "center", vertical: "top" }}
        message={"There Might Be New Conversations"}
        action={
          <IconButton
            onClick={() => {
              setNewConversation(false);
              handleFetchSessions();
            }}
          >
            <ReplayOutlined style={{ color: "white" }}></ReplayOutlined>
          </IconButton>
        }
      ></Snackbar>
      {OpenAddConversationModal ? (
        <AddConversation
          alreadyConnectedUsers={alreadyConnectedUsers}
          onClose={() => setOACM(false)}
          onSessionCreated={() => {
            handleFetchSessions();
          }}
        ></AddConversation>
      ) : (
        ""
      )}
      <Responsive {...Responsive.onlyComputer}>
        <Grid container>
          <Grid item sm={5}>
            <Conversations
              onAddConversation={() => {
                setOACM(true);
              }}
            ></Conversations>
            {loading ? (
              <div
                style={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  height: "50vh",
                }}
              >
                <CircularProgress style={{ color: "black" }}></CircularProgress>
              </div>
            ) : (
              ""
            )}
            {error ? (
              <div
                style={{
                  textAlign: "center",
                  marginTop: "10%",
                  fontFamily: "poppins",
                  fontWeight: "bold",
                }}
              >
                Error Fetching Conversations!!
              </div>
            ) : (
              ""
            )}
            <ChatSessions
              setSession={(id) => {
                setSession(id);
                handleMakeZero(id);
              }}
              sessions={sessions}
              session={session}
              MyId={props.MyId}
            ></ChatSessions>
          </Grid>
          <Grid item sm={6}>
            {session ? (
              <ChatWindow
                socket={socket}
                session={session}
                onBack={() => {
                  setSession(null);
                }}
                history={props.history}
                personNextSide={
                  sessions.find((res) => {
                    return res.session === session;
                  }).personNextSide
                }
              ></ChatWindow>
            ) : (
              ""
            )}
          </Grid>
          <Grid item sm={1}></Grid>
        </Grid>
      </Responsive>
      <Responsive {...Responsive.onlyMobile}>
        {!session ? (
          <Conversations
            onAddConversation={() => {
              setOACM(true);
            }}
          ></Conversations>
        ) : (
          ""
        )}
        {loading ? (
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              height: "50vh",
            }}
          >
            <CircularProgress style={{ color: "black" }}></CircularProgress>
          </div>
        ) : (
          ""
        )}
        {session ? (
          <ChatWindow
            socket={socket}
            session={session}
            onBack={() => {
              setSession(null);
            }}
            history={props.history}
            personNextSide={
              sessions.find((res) => {
                return res.session === session;
              }).personNextSide
            }
          ></ChatWindow>
        ) : (
          <ChatSessions
            setSession={(id) => {
              setSession(id);
              handleMakeZero(id);
            }}
            sessions={sessions}
            session={session}
            MyId={props.MyId}
          ></ChatSessions>
        )}
      </Responsive>
      <Responsive {...Responsive.onlyTablet}>
        <Grid container>
          <Grid item sm={5}>
            <Conversations
              onAddConversation={() => {
                setOACM(true);
              }}
            ></Conversations>
            {loading ? (
              <div
                style={{
                  height: "50vh",
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <CircularProgress style={{ color: "black" }}></CircularProgress>
              </div>
            ) : (
              <ChatSessions
                setSession={(id) => {
                  setSession(id);
                  handleMakeZero(id);
                }}
                sessions={sessions}
                session={session}
                MyId={props.MyId}
              ></ChatSessions>
            )}
          </Grid>
          <Grid item sm={7}>
            {session ? (
              <ChatWindow
                socket={socket}
                session={session}
                onBack={() => {
                  setSession(null);
                }}
                history={props.history}
                personNextSide={
                  sessions.find((res) => {
                    return res.session === session;
                  }).personNextSide
                }
              ></ChatWindow>
            ) : (
              ""
            )}
          </Grid>
        </Grid>
      </Responsive>
    </React.Fragment>
  );
};

const Conversations = ({ onAddConversation }) => {
  return (
    <div
      style={{
        fontWeight: "bold",
        fontSize: 30,
        padding: 20,
        fontFamily: "poppins",
        display: "flex",
        justifyContent: "center",
      }}
    >
      <div style={{ flexGrow: 1, textAlign: "center" }}>Conversations</div>
      <IconButton
        onClick={() => {
          onAddConversation();
        }}
      >
        <AddOutlined></AddOutlined>
      </IconButton>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state.userReducer.token,
    MyId: state.userReducer.id,
  };
};
export default connect(mapStateToProps)(Chat);
