import React from "react";
import "./chat.css";
import url from "../../strings/url";
import moment from "moment";
import { blue } from "@material-ui/core/colors";
import { Avatar, IconButton } from "@material-ui/core";
import { CircularProgress } from "@material-ui/core";
import { Result } from "antd";
import { connect } from "react-redux";
import { ArrowBack } from "@material-ui/icons";
class ChatWindow extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      clicked: true,
      id: 1,
      messages: [],
      chatText: "",
      seen: false,
      myId: null,
      PersonNextSide: null,
      chatsLoading: true,
      error: false,
      session: props.session,
      comingAnimation: "comingFromRightP5",
    };
    this.session = null;
    this.token = props.token;
    this.socket = props.socket.emit("Save_Id", {
      token: this.token,
      session: props.session,
    });
  }

  componentDidMount() {
    ///setting on message handler
    this.socket.on("Seen", (data) => {
      console.log(data);
      this.setState(({ messages }) => {
        return {
          messages: messages.map((message) => ({ ...message, seen: true })),
        };
      });
    });
    this.socket.on("message", (data) => {
      this.setState((pre) => {
        return { messages: pre.messages.concat([data]) };
      });
    });

    /// getting already chats for this session
    const headers = new Headers();
    headers.append("Authorization", this.props.token);
    headers.append("content-type", "application/json");
    this.session = this.props.session;
    fetch(`${url}/getSessionChats/${this.props.session}`, {
      method: "GET",
      headers,
    })
      .then(async (res) => {
        if (res.ok) {
          const ans = await res.json();

          setTimeout(() => {
            this.setState((prev) => ({
              messages: [...ans.messages, ...prev.messages],
              PersonNextSide: ans.PersonNextSide.participant,
              myId: ans.myId,
              chatsLoading: false,
              error: false,
            }));
          }, 200);
          return;
        }
        this.setState({ chatsLoading: false, error: true });
      })
      .catch((e) => {
        this.setState({ chatsLoading: false, error: true });
      });
  }
  componentWillUnmount() {
    this.socket.emit("updateSession", {
      token: this.token,
      session: null,
    });
  }
  componentDidUpdate() {
    if (!this.state.chatsLoading && !this.state.error) this.el.scrollIntoView();
    if (this.session !== this.props.session) {
      this.setState({ messages: [] });
      this.socket.emit("updateSession", {
        token: this.token,
        session: this.props.session,
      });
      this.setState({ chatsLoading: true });
      const headers = new Headers();
      headers.append("Authorization", this.props.token);
      headers.append("content-type", "application/json");
      this.session = this.props.session;
      fetch(`${url}/getSessionChats/${this.props.session}`, {
        method: "GET",
        headers,
      })
        .then(async (res) => {
          if (res.ok) {
            const ans = await res.json();

            return this.setState((prev) => ({
              messages: [...ans.messages, ...prev.messages],
              PersonNextSide: ans.PersonNextSide.participant,
              myId: ans.myId,
              chatsLoading: false,
              error: false,
            }));
          }
          this.setState({ chatsLoading: false, error: true, messages: [] });
        })
        .catch((e) => {
          this.setState({ chatsLoading: false, error: true, messages: [] });
        });
      this.session = this.props.session;
    }
  }

  render() {
    let { chatText, messages, seen } = this.state;

    return (
      <React.Fragment>
        <div className={`parentChatContainer ${this.state.comingAnimation}`}>
          <TopDetails
            history={this.props.history}
            dispatch={this.props.dispatch}
            onBack={() => {
              this.props.onBack();
            }}
            onBackAnimation={() => {
              this.setState({ comingAnimation: "goBack" });
            }}
            history={this.props.history}
            {...this.props.personNextSide}
          ></TopDetails>
          <div className="chatFormContainer">
            <ChatInput
              chatText={chatText}
              onChange={(val) => {
                this.setState({ chatText: val });
              }}
              onSend={this.onSendHandler}
            ></ChatInput>
          </div>
          {this.state.error && !this.state.chatsLoading ? (
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                height: "70vh",
              }}
            >
              <Result status="success" title="No Chats"></Result>
            </div>
          ) : (
            ""
          )}
          {this.state.chatsLoading ? (
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                height: "70vh",
              }}
            >
              <CircularProgress style={{ color: "black" }}></CircularProgress>
            </div>
          ) : (
            <ChatRenderer
              setRef={(ref) => {
                this.el = ref;
              }}
              PersonNextSide={this.state.PersonNextSide}
              messages={messages}
              myId={this.state.myId}
              socket={this.socket}
            ></ChatRenderer>
          )}
        </div>
      </React.Fragment>
    );
  }

  onSendHandler = () => {
    this.socket.emit("Chat_Message", {
      message: this.state.chatText,
      token: this.token,
      sessionId: this.session,
    });
    this.setState((prev) => {
      return { chatText: "" };
    });
  };
}

const ChatInput = ({ chatText, onChange, onSend }) => {
  return (
    <form
      className="ChattingForm"
      onSubmit={(e) => {
        e.preventDefault();
      }}
    >
      <input
        value={chatText}
        type="text"
        onChange={(e) => {
          onChange(e.target.value);
        }}
        className="chatInputField"
        placeholder="Type Something..."
      ></input>
      <button
        className="sendButton"
        disabled={chatText.length === 0}
        onClick={() => {
          onSend();
        }}
      >
        <i className="far fa-paper-plane"></i>
      </button>
    </form>
  );
};

const ChatRenderer = ({ messages, setRef, myId, PersonNextSide }) => {
  return (
    <div style={{ display: "flex", justifyContent: "center" }}>
      <div style={{ width: "100%" }}>
        <div
          id="chatCont"
          className="chatContainer"
          style={{ paddingTop: "100px" }}
        >
          {messages.map((m, index) => {
            return (
              <ChatMessage
                key={index}
                message={m.message}
                hasSeen={myId === m.from ? true : false}
                on={m.date}
                seen={m.seen}
                PersonNextSideProfilePhoto={PersonNextSide.profilephoto}
                by={
                  myId === m.from
                    ? "me"
                    : PersonNextSide.firstName + " " + PersonNextSide.LastName
                }
              ></ChatMessage>
            );
          })}
          <div
            ref={(ref) => {
              setRef(ref);
            }}
          ></div>
        </div>
      </div>
    </div>
  );
};

const ChatMessage = ({
  message,
  on,
  seen,
  hasSeen,
  by,
  PersonNextSideProfilePhoto,
}) => {
  return (
    <div
      className={
        by !== "me"
          ? "chatLeftItem comingFromLeftP5"
          : "chatRightItem comingFromUpP5"
      }
    >
      <div
        style={{
          // boxShadow: "0px 0px 10px rgba(0,0,0,0.2)",
          display: "flex",
          marginBottom: 10,
          marginTop: 10,
          backgroundColor: by !== "me" ? "rgba(0,0,0,0.01)" : "",
          borderRadius: 10,
          padding: 10,
          flexDirection: "column",
          alignItems: "flex-start",
          border: "0.5px solid rgba(0,0,0,0.1)",
        }}
      >
        <div
          style={{
            fontSize: 12,
            display: "flex",
            alignItems: "center",
          }}
        >
          {by !== "me" ? (
            <Avatar
              style={{ marginRight: 6, width: 20, height: 20 }}
              src={PersonNextSideProfilePhoto}
            ></Avatar>
          ) : (
            ""
          )}
          {by === "me" ? "You" : by}
        </div>
        <div>{message}</div>
        <div
          style={{ display: "flex", justifyContent: "center", marginTop: 7 }}
        >
          <div style={{ fontWeight: "lighter", fontSize: 12 }}>
            {moment(on).format("MMMM Do YYYY h mm ss a")}
          </div>
          {hasSeen ? (
            <i
              className={`far fa-eye ${seen ? "seen" : ""}`}
              style={{ color: seen ? blue[600] : "grey", marginLeft: 10 }}
            ></i>
          ) : (
            ""
          )}
        </div>
      </div>
    </div>
  );
};

const TopDetails = ({
  firstName,
  LastName,
  history,
  profilephoto,
  onBack,
  id,
  Online,
  onBackAnimation,
  dispatch,
}) => {
  return (
    <div
      style={{
        position: "absolute",
        top: 0,
        height: "60px",
        width: "100%",
        background: "white",
        borderBottom: "0.5px solid rgba(0,0,0,0.2)",

        zIndex: 1000,
        display: "flex",
        alignItems: "center",
      }}
    >
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <IconButton
          onClick={() => {
            setTimeout(() => {
              onBack();
            }, 500);
            onBackAnimation();
          }}
        >
          <ArrowBack></ArrowBack>
        </IconButton>
      </div>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          cursor: "pointer",
          fontFamily: "poppins",
          fontWeight: "bold",
          width: "100%",
          marginLeft: "2%",
        }}
        className="topDetails"
        onClick={() => {
          history.push(`/anonymity/person/${id}`);
          dispatch({ type: "Person" });
        }}
      >
        <div style={{ position: "relative" }}>
          <Avatar style={{ width: 40, height: 40 }} src={profilephoto}></Avatar>
          <div
            style={{
              position: "absolute",
              bottom: 0,
              height: "1.5vh",
              width: "1.5vh",
              borderRadius: "50%",
              background: Online.status === "Online" ? "green" : "transparent",
              transition: "1s ease",
              transform:
                Online.status !== "Online"
                  ? "translateX(30px) translateY(-34px)"
                  : "translateX(0px) translateY(0px)",
            }}
          ></div>
        </div>
        <div style={{ fontSize: 15, marginLeft: "2%" }}>
          {firstName + " " + LastName}
          <div style={{ fontSize: 11 }}>
            {Online.status === "Online"
              ? "Online"
              : Online.lastSeen
              ? "Last Seen.." +
                moment(Online.lastSeen).format("MMMM Do YYYY, h:mm a")
              : ""}
          </div>
        </div>
      </div>
    </div>
  );
};

export default connect((state) => ({ token: state.userReducer.token }))(
  ChatWindow
);
